/*个人中心*/
.person-center-page{padding-top: 30px;padding-bottom: 100px;}
.person-center-page .p1th{}
.person-center-page .p1th .lbox{margin-right: 20px;margin-top: 10px;}
.person-center-page .p1th .lbox img{border-radius: 50%;}

.person-center-page .p1th .cbox{}
.person-center-page .p1th .cbox h3{font-size: 30px;font-weight: normal;}
.person-center-page .p1th .cbox .p1{color: #aaaaaa;padding-top: 5px;}
.person-center-page .p1th .cbox .p2{color: #aaaaaa;font-size: 18px;padding-top: 3px;}
.person-center-page .p1th .cbox .dtable{color: #666666;font-size: 14px;}
.person-center-page .p1th .cbox .dtable td{padding-top: 3px;}
.person-center-page .p1th .cbox .icon-com{width: 18px;height: 18px;vertical-align: middle;display: inline-block;margin-right: 5px;}
.person-center-page .p1th .cbox .icon-phone{background: url(../../image/task/phone-18x18.png);}
.person-center-page .p1th .cbox .icon-place{background: url(../../image/task/place-18x18.png);}
.person-center-page .p1th .cbox .icon-experience{background: url(../../image/task/experience-18x18.png);}
.person-center-page .p1th .cbox .icon-need-person{background: url(../../image/task/need-person-18x18.png);}
.person-center-page .p1th .cbox .icon-url{background: url(../../image/task/url-18x18.png);}
.person-center-page .p1th .rbox{}
.person-center-page .p1th .rbox .start-wrap{border-radius: 5px;padding: 3px 10px 4px 10px;border: 1px solid #aaaaaa;}
.person-center-page .p1th .rbox .icon-start{background: url(../../image/task/start-16x16.png);width: 16px;height: 16px;display: inline-block;vertical-align: -3px;}
.person-center-page .p1th .cbox .dtable td .long-txt-cut{width: 150px;display: inline-block;}

.person-center-page .p1th .rbox .start-gray {vertical-align: -4px}


.person-center-page .p2th{padding: 50px 0 0 0;width: 960px;overflow: hidden;}
.person-center-page .p2th .scrollBox{width:990px;position: relative;background: #000;}
.person-center-page .p2th .scrollBox .img_list li{float:left;display:inline;width:310px;height:380px;overflow:hidden;margin-right: 15px;background-color: #000;}
.person-center-page .p2th .scrollBox .img_list li img{max-width: 310px;max-height: 380px}

.person-center-page .p2th .scrollBox .page-wrap{position: absolute;top: 160px;left: 0px;width: 960px;}
.person-center-page .p2th .scrollBox .pre{display:inline;background:url(../../image/task/bnt-left-50x50.png);width:50px;height:50px;overflow:hidden;margin-right:10px;}
.person-center-page .p2th .scrollBox .next{display:inline;background:url(../../image/task/bnt-right-50x50.png);width:50px;height:50px;overflow:hidden;float:right;display:inline;  }
.person-center-page .p2th .scrollBox .page-box a{display:inline;float:left;background:url(../../image/task/circle-12x12-n.png);width:12px;height:12px;overflow:hidden;margin-right:10px;text-indent:-9999px;}
.person-center-page .p2th .scrollBox .page-box a.selected{background:url(../../image/task/circle-12x12-s.png);}
.person-center-page .p2th .des{padding-top: 50px;font-size: 14px;padding-bottom: 50px;width: 840px;}
.person-center-page .p2th .des .title{color: #fe9920;padding-bottom: 10px;}
.person-center-page .p2th .des .info{line-height: 24px;color: #666666;text-indent: 24px;}
.person-center-page .p3th{width: 100%;background-color: #f2f2f2;padding: 30px 0;}

.person-center-page .p3th .title{color: #fe9920;padding-bottom: 10px;}

.person-center-page .p3th .son-wrap .item-wrap{}
.person-center-page .p3th .son-wrap .item-wrap .item{float:left;display:inline; width: 270px;margin-right: 25px; }
.person-center-page .p3th .son-wrap .item-wrap li{width: 240px;border-radius: 10px;background-color: #fff;border: 1px solid #e6e6e6;padding: 10px;margin-bottom: 20px;}
.person-center-page .p3th .son-wrap .item-wrap .h4{color: #000000;}
.person-center-page .p3th .son-wrap .item-wrap .des{color: #aaaaaa;padding-top: 10px;}


.person-center-page .p3th .son-wrap .item-wrap2{}
.person-center-page .p3th .son-wrap .item-wrap2 .item{}
.person-center-page .p3th .son-wrap .item-wrap2 li{width: 240px;border-radius: 10px;background-color: #fff;border: 1px solid #e6e6e6;padding: 10px;margin-bottom: 20px;float:left;display:inline;  margin-right: 25px;}
.person-center-page .p3th .son-wrap .item-wrap2 .h4{color: #000000;}
.person-center-page .p3th .son-wrap .item-wrap2 .des{color: #aaaaaa;padding-top: 10px;}


.person-center-page .p4th{}
.person-center-page .p4th .title{color: #fe9920;padding: 30px 0 0 0;}
.person-center-page .p4th .wbox{color: #666666;}
.person-center-page .p4th .wbox .lbox{margin-right: 20px;}
.person-center-page .p4th .wbox .lbox img{max-height: 60px; max-width: 60px;}
.person-center-page .p4th .rbox .start-wrap{font-size: 14px;font-weight: bold;}
.person-center-page .p4th .rbox .icon-start{background: url(../../image/task/start-16x16.png);width: 16px;height: 16px;display: inline-block;vertical-align: middle;}
.person-center-page .p4th .rbox .p1{padding-top: 5px;}
.person-center-page .p4th .rbox .p2{padding-top: 15px;}
.person-center-page .p4th  li{border-bottom: 1px solid #e6e6e6;padding: 20px 0 15px 0;}
.person-center-page .p4th  li:nth-last-of-type(1){
    border: none;
}
.person-center-page .p5th{border-top: 1px solid #e6e6e6;margin-top: 30px;color: #666666;}
.person-center-page .p5th .title{font-size: 14px;color: #fe9920;padding-top: 30px;padding-bottom: 0px;}
.person-center-page .p5th li{margin-top: 10px;}
.person-center-page .p5th li h4{padding: 3px 0; }
.person-center-page .p5th .np-tips{padding:12px 20px;border: 1px solid #e6e6e6;width: 500px;margin: 0 auto;margin-top: 50px;text-align: center;}
